import React, { Component } from 'react'
import PropTypes from 'prop-types'
import './ProductsListItem.css'
import QuantityInput from '../../../common/features/Quantity/QuantityInput';
import {Link} from 'react-router-dom'
import {connect} from 'react-redux'

class ProductsListItem extends Component {

    static defaultProps = {
        isLiked: false,
    }

    static propTypes = {
        name: PropTypes.string.isRequired,
        description: PropTypes.string,
        type: PropTypes.string,
        capacity: PropTypes.number,
        price: PropTypes.number.isRequired,
    }

   // static defaultProps = {
   //     description:"No description"
   // }

    state = {
        productsCount: 1,
    }

    onIncrementClick = () => {
        this.setState((prevState)=>({
            productsCount: prevState.productsCount + 1
        }))
    }

    onDecrementClick = () => {
        this.setState((prevState)=>({
            productsCount: prevState.productsCount - 1
        }))
    }

    renderProductCount () {
        return (
                <QuantityInput
                    minCount={1}
                    onDecrementClick={this.onDecrementClick}
                    onIncrementClick={this.onIncrementClick}
                    productsCount={this.state.productsCount}
                />
                )
    }      
    onLikeClick = () => {
        if (this.props.isLiked) {
            this.props.dispachDisLike(this.props.id)
        } else {
            this.props.dispachLike(this.props.id)
        }
    }


    render () {

        const {
            id,
            name,
            type,
            description = "No description...",
            price,
            capacity,
            image,
            addProductToCart,
            isLiked
        } = this.props

        return (
            <div className="products-list-item">
                <div className="product-image">
                    <img src={image} alt={name}/>
                </div>
                <button onClick={() => this.onLikeClick()}>
                    {isLiked ? <span>&#9829;</span> : <span>&#9825;</span>}
                </button>
                <div className="product-name">
                    <Link to={`products/${id}`}>{name}</Link></div>
                <div className="product-description">{description}</div>
                <div className="product-type">Type: {type}</div>
                <div className="product-capacity">Capacity: {capacity} Gb</div>
                {this.renderProductCount()}
                <div className="product-price">$ {price}</div>
                <button 
                    className="btn-add-to-cart"
                    onClick={() => addProductToCart(id,this.state.productsCount)}>Add to cart</button>
            </div>
        )
    }
}

const mapStateToProps = (state,props)=> {
    return {
        isLiked:state.likeProducts[props.id]
    }
}

const mapDispatchToProps = (dispach) =>({
    dispachLike: (id) => dispach({
        type:"LIKE",
        id: id,
    }),
    dispachDisLike: (id) => dispach({
        type:"DISLIKE",
        id: id,
    }),
    addProductToCart: (id,count) => dispach({
        type: "ADD_PRODUCT_TO_CART",
        id:id,
        count:count,
    })
    
})

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(ProductsListItem)